  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-font: "Roboto", sans-serif;
  --main-text-color: #2c2c2c;
  --secondary-text-color: #000;
  --btn-default: #bbf330;
  --btn-onhover: #b5ff00;
  --bgc-main: #fefefe;
  --bgc-secondary: #fff;
  --dark-green: #7eb101;
  --border-onhover: #597d00;
  --yellow-main: #ffe550;
  --error-color: #ff705d;
  --radial-gradient: #2f583f@ 0% rgba(105, 105, 105, 0)@ 100%;
  --items: 3;
  --indent: 32px;
  --btn-input-radius: 30px;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

.subscribe__btn, .offerings__order-now-button, .hero__btn, .btn-order {
  border-radius: var(--btn-input-radius);
  color: var(--main-text-color);
  font-size: 14px;
  line-height: 1.5;
}

.subscribe__btn:hover, .offerings__order-now-button:hover, .hero__btn:hover, .btn-order:hover, .subscribe__btn:focus, .offerings__order-now-button:focus, .hero__btn:focus, .btn-order:focus {
  cursor: pointer;
}

h1, h2, h3, p, ul, li {
