  transition: fill var(--animation);
}

.burger-menu:hover .burger-menu__svg, .burger-menu:focus .burger-menu__svg {
  fill: var(--yellow-main);
}

.menu-close {
  transform: translate(-50% -50%);
  margin-top: 4px;
  margin-right: 4px;
  position: absolute;
  top: 0;
  right: 0;
}

.menu-backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: opacity var(--animation), visibility var(--animation);
  background-color: 0px 4px 8px rgba(0, 0, 0, .04), 0px 2px 4px rgba(0, 0, 0, .08), 0px 1px 3px rgba(0, 0, 0, .16);
  padding-left: 20px;
  padding-right: 20px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.menu-backdrop.is-open {
  opacity: 1;
  pointer-events: initial;
  visibility: visible;
}

.menu-container {
  max-width: 130px;
  background-color: var(--bgc-main);
  transition: transform var(--animation);
  border-radius: 30px 0 30px 30px;
  margin-top: 50px;
  margin-left: auto;
  overflow: hidden;
  transform: translateX(100%);
}

